<template>
  <div class="product">
    <!-- <div class="topNavBar"> -->
    <van-nav-bar
    title="商品详情"
    left-text="返回"
    left-arrow
    @click-left="$router.go(-1)"
    />
    <van-swipe class="my-swipe" :autoplay="3000">
      <van-swipe-item v-for="item in goods.goods_images" :key="item.file_id">
        <img :src="item.external_url" alt=""  @click="imageDetail">
      </van-swipe-item>
    </van-swipe>
    <van-goods-action>
      <van-goods-action-icon icon="wap-home-o" text="主页" dot @click="$router.push('/')"/>
      <van-goods-action-icon icon="cart-o" text="购物车" :badge="cartTotal" @click="$router.push('/cart')"/>
      <van-goods-action-button type="warning" text="加入购物车" @click="addToCart"/>
      <van-goods-action-button type="danger" text="立即购买" @click="buynow"/>
    </van-goods-action>
    <div class="price">
      ￥{{goods.goods_price_min}}
      <span class="minPrice">{{goods.goods_price_max}}</span>
      <span class="sales">已售{{goods.goods_sales}}件</span>
    </div>
    <div class="name" v-text="goods.goods_name"></div>
    <div class="service">
      <span>
        <van-icon name="certificate" color="#ee0a24" size="20"/>
        七天无理由退货
      </span>
      <span>
        <van-icon name="certificate" color="#ee0a24" size="20"/>
        48小时发货
      </span>
      <van-icon name="arrow" class="right_arrow" />
    </div>
    <div class="comment">
       <p>
          <span>商品评价（{{commentsOverview.all}}条）</span>
          <span @click="toReviewDetailPage">
            更多评价
            <van-icon name="arrow"/>
          </span>
       </p>
      <div class="review" v-for="item in comments" :key="item.comment_id">
        <van-icon v-if="item.user.avatar_url==null" name="contact-o"/>
        <img v-else :src="item.user.avatar_url" />
        <span>{{item.user.nick_name}}</span>
        <van-rate
          :value="item.score/2"
          :size="16"
          color="#ffd21e"
          void-icon="star"
          void-color="#eee"
          readonly
        />
        <div class="content">
         {{item.content}}
        </div>
        <div class="time">{{item.create_time}}</div>
       </div>
    </div>
    <van-action-sheet v-model="show" title="加入购物车">
      <div class="content">
        <img :src="goods.goods_image"/>
        <div class="price">￥{{goods.goods_price_min}}</div>
        <div class="stock">库存{{goods.stock_total}}</div>
        <p>
          <span>数量</span>
          <countBox v-model="productCount" class="countBox"/>
        </p>
        <p class="footer">
          <addButton class="addButton" @button-clicked="handleAddToCart">
            <template v-slot:content>加入购物车</template>
          </addButton>
        </p>
      </div>
    </van-action-sheet>
    <van-action-sheet v-model="showPurchase" title="立即购买">
      <div class="content">
        <img :src="goods.goods_image"/>
        <div class="price">￥{{goods.goods_price_min}}</div>
        <div class="stock">库存{{goods.stock_total}}</div>
        <p>
          <span>数量</span>
          <countBox v-model="productCount" class="countBox"/>
        </p>
        <p class="footer">
          <addButton class="addButton" @button-clicked="handleBuynow">
            <template v-slot:content>立即购买</template>
          </addButton>
        </p>
      </div>
    </van-action-sheet>
  </div>
</template>

<script>
import { getProductDetail, getCommentsNumber, getAllCommments } from '@/api/productDetail'
import { ImagePreview, Dialog } from 'vant'
import { addToCartApi } from '@/api/cart'
import countBox from '@/components/countBox.vue'
import addButton from '@/components/myButtonVer1.vue'

export default {
  name: 'ProductIndex',
  components: {
    [ImagePreview.Component.name]: ImagePreview.Component,
    countBox,
    addButton
  },
  data () {
    return {
      goods: {},
      comments: {},
      commentsOverview: {},
      show: false,
      productCount: 1,
      cartTotal: undefined,
      showPurchase: false
    }
  },
  async created () {
    this.goods = await getProductDetail(this.$route.query.goodsId)
    this.goods = this.goods.data.detail
    // console.log('goods', this.goods)
    // console.log('params:', this.$route.query.goodsId)

    this.commentsOverview = await getCommentsNumber(this.$route.query.goodsId)
    this.commentsOverview = this.commentsOverview.data.total
    // console.log('Overview:', this.commentsOverview)

    this.comments = await getAllCommments(this.$route.query.goodsId, 5)
    this.comments = this.comments.data.list
    // console.log('comments:', this.comments)
  },
  methods: {
    imageDetail () {
      // console.log('abcs')
      const images = []
      this.goods.goods_images.forEach(element => {
        images.push(element.external_url)
      })
      ImagePreview({
        images,
        closeable: true
      })
    },
    toReviewDetailPage () {
      this.$router.push(`/reviewDetail?goodsId=${this.$route.query.goodsId}`)
    },
    addToCart () {
      this.show = true
    },
    buynow () {
      this.showPurchase = true
    },
    async handleBuynow () {
      if (this.$userLocalStorage.getLocalState() === false) {
        Dialog.confirm({
          title: '您还没有登录哦:(',
          // message: '弹窗内容'
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // on confirm
            this.$router.replace(`/login?goodsId=${this.$route.query.goodsId}`)
          })
          .catch(() => {
            // on cancel
          })
        return
      }
      this.$router.push(`/submit?goodsId=${this.goods.goods_id}&goodsNum=${this.productCount}&goodsSku=${this.goods.skuList[0].goods_sku_id}`)
    },
    async handleAddToCart () {
      // validate if the user has logged in
      // console.log('user:', this.$store.state.user.userInfo)
      if (this.$userLocalStorage.getLocalState() === false) {
        Dialog.confirm({
          title: '您还没有登录哦:(',
          // message: '弹窗内容'
          confirmButtonText: '去登录',
          cancelButtonText: '再逛逛'
        })
          .then(() => {
            // on confirm
            this.$router.replace(`/login?goodsId=${this.$route.query.goodsId}`)
          })
          .catch(() => {
            // on cancel
          })
        return
      }
      // console.log(this.goods.goods_id, this.productCount, this.goods.skuList[0].goods_sku_id)
      const res = await addToCartApi(this.goods.goods_id, this.productCount, this.goods.skuList[0].goods_sku_id)
      if (res.status === 200) {
        this.cartTotal = res.data.cartTotal
      }
      // console.log('toCart:', res)
    }
  }
}
</script>

<style scoped>
.van-swipe-item img{
  width: 100%;
}
.price{
  /* border: 1px solid black; */
  color: red;
  position: relative;
}
.price .minPrice{
  font-size: 15px;
  color: #cbcbcb;
  text-decoration: line-through;
}
.price .sales{
  font-size: 15px;
  color: #aeaeae;
  position: absolute;
  right: 10px;
  top: 0;
}
.service{
  position: relative;
  background-color: #cccccc30;
}
.service .right_arrow{
  position: absolute;
  right: 0;
}
.comment p {
  position: relative;
  margin-top: 20px;
}
.comment p span:last-child{
  /* background-color: #e64646; */
  color: #aeaeae;
  font-size: 15px;
  position: absolute;
  right: 0;
}
.comment .review{
  /* border: 1px solid black; */
  padding: 10px;
}
.comment .review .content{
    max-height: 60px;
    overflow: scroll;
}
.comment .review span:first-child{
  margin: 0 10px;
  background-color: #d63131;
}
.comment .review .time{
  color: #ccc;
  font-size: 15px;
  text-align: right;
}
.comment .review img{
  width: 20px
}
.van-action-sheet .content img{
  width: 100px;
}
.van-action-sheet .content p{
  position: relative;
  /* border: 1px solid black; */
  height: 35px;
}
.van-action-sheet .content p span{
  position: absolute;
  top: 0;
  left: 10px;
}
.van-action-sheet .content p .countBox{
  position: absolute;
  top: 0;
  right: 10px;
}
.van-action-sheet .content .footer{
  /* background-color: #d6b0b0; */
  text-align: center;
}
</style>
